<template>
    <div>
        <form v-if="!isNan">
            <p>登录名</p>
            <input type='text' v-model="name">
            <p>密码</p>
            <input type='pass' v-model="password">
            <button type="button" @click='login()'>登入</button>
            <button type="button" @click="reg()">注册</button>
        </form>
        <form v-else>
            <p>登录名</p>
            <input type='text' v-model="name">
            <p>密码</p>
            <input type='pass' v-model="password">
            <p>再次输入密码</p>
            <input type="pass" v-model='repeat'>
            <div>
                <button type="button" @click="add()">确定</button>
                <button type="button" @click="cencel()">取消</button>
            </div>
            
        </form>
        
    </div>
</template>

<script>
    export default{
        name:"Login",
        data(){
            return{
                isNan:false,
                name:'',
                password:'',
                repeat:''
            }
        },
        methods:{
            login(){
                if(localStorage.getItem('name') == this.name && localStorage.getItem('password') == this.password){
                    this.name =''
                    this.password = ''
                    this.$router.push('/home/list')
                }else{
                    alert('用户名或密码错误')
                }
                
            },
            reg(){
                this.isNan = true
            },
            cencel(){
                this.isNan =  false
            },
            add(){
                if(this.password === this.repeat){
                    localStorage.setItem('name',this.name)
                    localStorage.setItem('password',this.password)
                    this.name=''
                    this.password=''
                    this.isNan=false
                }else{
                    alert('两次密码不匹配')
                } 
            }
        }
    }
</script>

